/*css for polaroids begins*/


.polaroid {
  background: #fff;
  padding: 1rem;
  box-shadow: 0 0.2rem 1.2rem rgba(0,0,0,0.5);
  
}
.polaroid > img{
  max-width: 100%;
  height: auto;
}
.caption {
  color: #5C5959;
  font-family: Dancing Script, Snell Roundhand, Bradley Hand, Brush Script MT;
  font-size: 1.9rem;
  text-align: center;
  line-height: 2em;

}
.item {
  width: 75%;
  margin:auto;
  height:fit-content;
  display: flex;
  display: -webkit-box;
  position:relative;
  filter: grayscale(100%);
z-index:10;
}
.item .polaroid:before {
  content: '';
  position: absolute;
  z-index: -1;
  transition: all 0.35s;
}

.item {
  transform: scale(0.8, 0.8) rotate(3deg);
  transition: all 0.35s;
}
.item .polaroid:before {
  height: 20%;
  width: 57%;
  bottom: 0px;
  box-shadow: 0 2.1rem 2rem rgba(0,0,0,0.5);
}

.item:hover {
  filter: none;
  transform: scale(1, 1) rotate(0deg) !important;
  transition: all 0.35s;
}

.item:hover .polaroid:before {
  content: '';
  position: absolute;
  z-index: -1;
  transform: rotate(0deg);
  height: 90%;
  width: 75%;
  bottom: 0%;
  box-shadow: 0 1rem 3rem rgba(0,0,0,0.5);
  transition: all 0.35s;
}


/*css for polaroids ends*/




