/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 999; /* Sit on top */
  padding-top: 1.5vh;
  padding-bottom: 15vh; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.modaltitle {
  margin:0;
  font-family:courier new;
  position:relative;
  color: $cyan;
  font-size:2.5rem;
}



/* Modal Content */
.modal-content {
  background-color: white;
  text-align:center;
  align-items:center;
  position:relative;
  padding:2rem;
  margin: auto;
  border: 1px solid #888;
  width: 90%;
  animation: modalfadein .5s ease-in;
  z-index:99 !important;
  color:black;
}

.modal-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2em;
}

/* The Close Button */
.close {
  color: #aaaaaa;
  position:absolute;
  top:.1vh;
  right:1vw;
  font-size: 3rem;
  font-weight: bold;
  z-index: 999 !important;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}


/*the bottom close button*/
#bottom {
  top:auto;
  bottom:.2rem;
}




/*Styling for the input/label/checked thing*/


input {
  position: relative;
  display: none;
  cursor: pointer;
  animation: scale-in-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

}

label {
  display: none;
  visibility: hidden;
  height:5rem;
  width:5rem;
  left:20px;
  top:1rem;
  position: absolute;
  background-repeat:no-repeat;
  background-image: url('./pics/mon3.png');
  background-size:cover; 
  z-index:99;
  transition: background 3s, transform 1s top 5s;
outline: none !important;
-webkit-tap-highlight-color: transparent;

}


label:hover {transform: translateY(-3px);
cursor: pointer;
}

/*Product is the png that says "dark mode"*/
label::before {
  content:"";
  background-image: url('./pics/darkarchsml.png');
  position:absolute;
background-size:contain;
background-repeat:no-repeat;
// background-position:center center;
  width:140%;
  height:140%;
top:-25%;
  // bottom:-70%;
  left:-17%;
  z-index:99999 !important;
  display:block;
}

/*Styling for the input/label/checked thing*/


/*so this is what happens when I click the moon*/
input:checked + label {
  background: url('./pics/flaresun.png');
  // background-color: rgba(rgb(255, 195, 42),0.9);
  // box-shadow: 0px 8px 15px rgba(rgb(255, 195, 42),0.9);
  // border-radius:50%;
 background-size:cover;
 background-repeat:no-repeat; 
 transition: background 1.5s;
 animation: newoffback 1.5s ease forwards;
}

// this is what happens when unchecking,(sun to moon) and also when page loads
input[type=checkbox]:not(:checked) + label {
  animation: newoffback2 1.5s ease;
  transition: background 1s;
}

input[type=checkbox]:not(:checked) + label::before {
  animation: fadeIn 3.5s ease-in-out infinite;
  animation-delay: 1s;
  opacity:0;
}
/*this is the "dark mode png*/


/*this is the "dark mode" png*/
input:checked + label::before {
   visibility: hidden;

}



@keyframes fadeIn {
0% {opacity:0;}
50% {opacity:1;
      filter: brightness(1.75);}
100% {opacity:0;}
}


