@keyframes newoffback {
 0% {
    transform: translate(0, 0);    
  }
  50% {
    transform: translate(-130px, 150px) ;
  }

 100% {transform: translate(0, 0) scale(1.5);} 

}

@keyframes newoffback2 {
 0% {
    transform: translate(0, 0) scale(1.5);    
  }
  50% {
    transform: translate(-130px, 150px);
  }

 100% {transform: translate(0, 0);} 

}



