// mything is the other light switch, after you scroll down

.mything {
  display:none;
  -webkit-appearance: none;
  position: fixed;
  width: 80px;
  height: 40px;
  display:block;
  top:8px;
  left:8px;
  background-image: url('./pics/newnight.jpg');
  border-radius: 20px;
