  outline: none;
  transition: background-image .90s;
  background-size: cover;
  background-repeat:no-repeat;
  // background-position: left center;
  box-shadow: 0px 1px 2.5px .5px gray;
}

.mything:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 40px;
  width: 40px;
  background-color: navy;
  border-radius: 20px;
  transition: all .9s;
  background-color:#ECF0F3;
  
}

.mything:checked {
