  margin:0 auto;
  text-align:left
}

#blurb {
  display: block;
padding-left:10%;
padding-right:10%;
position:relative;
margin:0 auto;
overflow-x:hidden;
padding-top:1.5rem;
font-size:1.1rem;
}

p {font-size:1.2rem;}


h2 {font-size:3rem;}


  
   #gridwrapper  {
    padding-left:8vw;
    padding-right:8vw;
    display:block;}



.mything {

  width: 50px;
  height: 25px;
  top:10px;
  left:10px;
  border-radius: 12.5px;
  box-shadow: 0px 1px 2.5px .5px gray;
}

.mything:before {
  height: 25px;
  width: 25px;
  border-radius: 12.5px;
}


.mything:checked {
  transition: background-image .90s;
background-size:109% 109%;
   background-position: -2px -1px;
}


.My-Projects {margin-bottom:0rem;
              }

.column h3 {
  font-family: courier new, Arial, Exo ;
}

/*these are the subheadings*/
h3.mystoryh3{
  font-family: Dancing Script, cursive,Bradley Hand, Lucida Handwriting, Snell Roundhand ;
  font-size:2.15rem;
}


}




@media screen and (max-width:400px){




  #blurb {
    margin-top:0vw;}




.arrow::after {
  content:"";
  width:15px;
  height:15px;
  position:absolute;
  margin: auto;
  top:3rem;
  left:0;
  right:0;
  bottom:0;
  border-right: 1px solid $cyan;
  border-bottom: 1px solid $cyan;}


}



/*this is so the bigger bg image only gets shown on bigger screens*/
@media screen and (min-width: 768px) {

.window::before {
  background-size: 100% 100%;
  background-image:url('./pics/oilday.jpg');
}


    


/*dark  */
.window::after {
  background-image:url('./pics/nightoil.jpg');
  background-size: 100% 100%;
}

}





/*below is for all ipads in portrait*/

@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : portrait) { 

.window::before {
  background-image:url('./pics/daymob.jpg');
}


/*dark  */
.window::after {
  background-image:url('./pics/nightmob.jpg');
}

.item {width:100%;}

#scroller2{bottom:0rem;}

.lastarrow {top:0vh;}

 }
























